<template>
    <div>
        <van-field v-model="account" placeholder="请输入账号" label="账号" autocomplete="new-password"/>
        <van-field v-model="password" placeholder="请输入密码" type="password" label="密码" autocomplete="new-password"/>
        <van-button type="primary" @click="handleLogin" block>登录</van-button>
    </div>
</template>

<script>

    import {Button, Field, Toast} from 'vant';
    import md5 from 'js-md5';

    export default {
        name: "Login",
        components: {
            [Field.name]: Field,
            [Button.name]: Button,
            [Toast.name]: Toast,
        },
        data() {
            return {
                account: "",
                password: "",
            }
        },
        methods: {
            handleLogin() {
                this.$axios.get("/user?filterByFormula=%7Baccount%7D='" + this.account + "'").then(response => {
                    let users = response.data.records || [];
                    if (users.length !== 1) {
                        Toast.fail('账号或密码错误!');
                        return;
                    }

                    let password = md5(users[0].id + '-' + this.password);
                    if (password !== users[0].fields.password) {
                        Toast.fail('账号或密码错误!');
                        return;
                    }

                    Toast.success('登录成功!');

                    this.$store.dispatch('syncPermissions', {}).then(() => {
                        this.$router.push("/");
                    });

                });
            }
        }
    }
</script>

